<template><div><h1>autoprefix 推荐配置</h1>
<p><code>vue</code>官方模板的设置是<code>last 2 versions</code>，相对来说支持的浏览器版本比较少，可能会导致你在某些<code>Android</code>机子上出现问题。</p>
<p>如果你使用 <code>last 7 versions</code> 会生成不必要的<code>-ms</code>前缀代码.</p>
<p>因此建议同<code>WeUI</code>一样，使用配置 <code>['iOS &gt;= 7', 'Android &gt;= 4.1']</code></p>
</div></template><script>
export default {
  head: {"title":" vue-loader autoprefix 推荐配置"}
}
  </script>


